class Portfolio extends React.Component {
  state = {
    modal: false,
  };

  toggle = () => {
    this.setState({
      modal: !this.state.modal,
    });
  };
  render() {
    return (
      <React.Fragment>
        <MDBCol>
          <MDBCard className="cards" style={{ width: "100%" }}>
            <MDBCardBody>
              <div className="centt">
                <Router>
                  <div>
                    <Link className="btn" to="/">
                    <i class="far fa-address-card"></i>  &nbsp; About Me
                    </Link>

                    <Link className="btn" to="/CV">
                    <i class="far fa-file"></i> &nbsp; Resume / CV
                    </Link>

                    <Link className="btn" to="/portfolioexamples">
                    <i class="far fa-folder-open"></i> &nbsp; Portfolio
                    </Link>

                    <Link className="btn" to="/Contact">
                    <i class="far fa-envelope"></i>&nbsp;   Contact me
                    </Link>

                    <hr />

                    {/*
          A <Switch> looks through all its children <Route>
          elements and renders the first one whose path
          matches the current URL. Use a <Switch> any time
          you have multiple routes, but you want only one
          of them to render at a timenp
        */}
                    <Switch>
                      <Route exact path="/">
                        <Home />
                      </Route>

                      <Route path="/portfolioexamples">
                        <Portfolioexamples />
                      </Route>
                      <Route exact path="/CV">
                        <CV />
                      </Route>
                      <Route exact path="/contact">
                        <Contact />
                      </Route>
                    </Switch>
                  </div>
                </Router>

                <hr></hr>
                <br></br>
              </div>
            </MDBCardBody>
          </MDBCard>
        </MDBCol>

        
      </React.Fragment>
    );
  }
}
export default Portfolio;

